<template>
  <div class="teacher-class-student-detail">
    <div class="teacher-class-header">
      <span class="fl">周慧琳的个人资料</span>
      <span class="back-btn" @click="back">返回</span>
    </div>
    <div class="first-panel">
      <div class="base">
        <div class="sub-head">
          <div class="title">基本资料</div>
        </div>
        <div class="box">
          <div class="img-box">
            <img :src="imgSrc" :draggable="false">
          </div>
          <div class="detail">
            <div class="name">{{name}}</div>
            <div class="line">
              <div class="half">
                <span class="label">性别：</span>
                {{gender}}
              </div>
              <div class="half last">
                <span class="label">籍贯：</span>
                {{native}}
              </div>
            </div>
            <div class="line">
              <div class="half">
                <span class="label">民族：</span>
                {{nation}}
              </div>
              <div class="half last">
                <span class="label">政治面貌：</span>
                {{political}}
              </div>
            </div>
            <div class="line">
              <div class="half">
                <span class="label">入学时间：</span>
                {{signInDate}}
              </div>
              <div class="half last">
                <span class="label">学籍号：</span>
                {{rollNumber}}
              </div>
            </div>
            <div class="line">
              <div class="half">
                <span class="label">出生年月：</span>
                {{birthday}}
              </div>
              <div class="half last">
                <span class="label">身份证号：</span>
                {{IDCard}}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="calendar">
        <div class="sub-head">
          <div class="title">签到日历</div>
          <div class="date">
            <div class="date-cover">
              {{yearMonth}}
              <i class="iconfont icon-rili"></i>
            </div>
            <el-date-picker
              v-model="yearMonth"
              :clearable="false"
              type="month"
              value-format="yyyy-MM"
              @change="pickYear"
              class="date-picker"
            ></el-date-picker>
          </div>
        </div>
        <div class="calendar-box" v-loading="calendarLoading">
          <div class="week-box flex-box">
            <div class="flex-item">日</div>
            <div class="flex-item">一</div>
            <div class="flex-item">二</div>
            <div class="flex-item">三</div>
            <div class="flex-item">四</div>
            <div class="flex-item">五</div>
            <div class="flex-item">六</div>
          </div>
          <div class="day-box">
            <div class="day-line flex-box" v-for="(dataItem,key) in dataArr" :key="key">
              <div
                v-for="(item,index) in dataItem.children"
                :key="index"
                class="flex-item"
                :class="{'color-gray':!item.isCurMon}"
                @click="chooseDay(key,index)"
              >
                <div class="date-item">
                  <span class="day" :class="{'cur-day':item.check}">{{item.day}}</span>
                  <sup
                    v-if="item.day % 10 == 0"
                    class="day-dot"
                    :class="{'blue-dot':item.day % 6 == 0}"
                  ></sup>
                </div>
                <div v-if="item.classNum > 0" class="class-item">{{item.classNum}}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="second-panel">
      <div class="grade">
        <div class="sub-head">
          <div class="title">学生成绩</div>
          <div class="btns">
            <span :class="{'choose-test':chooseTest==0}" @click="chooseTest=0">在线测验</span>
            <span :class="{'choose-test':chooseTest==1}" @click="chooseTest=1">月考</span>
            <span :class="{'choose-test':chooseTest==2}" @click="chooseTest=2">周考</span>
          </div>
          <div class="date">
            <div class="date-cover">
              {{yearMonth}}
              <i class="iconfont icon-rili"></i>
            </div>
            <el-date-picker
              v-model="yearMonth"
              :clearable="false"
              type="month"
              value-format="yyyy-MM"
              @change="pickYear"
              class="date-picker"
            ></el-date-picker>
          </div>
        </div>
        <el-table :data="tableData" class="grade-table" style="width: 100%">
          <el-table-column prop="name" label="科目名称"></el-table-column>
          <el-table-column prop="times" label="测试次数"></el-table-column>
          <el-table-column prop="rate" label="通过率"></el-table-column>
        </el-table>
        <div class="course-page" style="margin-top:18px">
          <el-pagination layout="prev, pager, next" :page-size="6" :total="allItems"></el-pagination>
        </div>
      </div>
      <div class="right">
        <div class="schedule">
          <div class="sub-head">
            <div class="title">学习进度</div>
          </div>
          <div class="box">
            <div class="schedule-item" v-for="(item,index) in scheduleList" :key="index">
              <div class="img-box">
                <img :src="item.img" :draggable="false">
              </div>
              <div class="desc">
                <div class="title">{{item.title}}</div>
                <el-progress :percentage="item.progress"></el-progress>
              </div>
            </div>
          </div>
        </div>
        <div class="done">
          <div class="sub-head">
            <div class="title">作业完成情况</div>
          </div>
          <div id="doneChart" style="width:100%;height:calc(100% - 40px)"></div>
          <div class="diy-legend">
            <span class="legend-item" v-for="(item,index) in homeWorkChartDatas" :key="index">
              <span class="icon" :style="{background:homeWorkChartColors[index]}"></span>
              <span class="text">{{item.name}}</span>
            </span>
          </div>
        </div>
      </div>
    </div>
    <div class="third-panel">
      <div class="chart">
        <div class="sub-head">
          <div class="title">平台学习时间</div>
        </div>
        <div class="catalog">
          <span
            class="date-btn"
            :class="{'choosed':catalogChoose == 2}"
            @click="catalogChoose = 2"
          >日</span>
          <span
            class="date-btn"
            :class="{'choosed':catalogChoose == 1}"
            @click="catalogChoose = 1"
          >月</span>
          <span
            class="date-btn"
            :class="{'choosed':catalogChoose == 0}"
            @click="catalogChoose = 0"
          >年</span>
          <span class="date">{{spendDate}}</span>
        </div>
        <div id="spendTime" style="width:100%;height:calc(100% - 72px)"></div>
        <div class="total-time">
          {{totalSpendTime}}
          <span>小时</span>
        </div>
      </div>
      <div class="chart">
        <div class="sub-head">
          <div class="title">知识专注度</div>
        </div>
        <div id="attention" style="width:100%;height:calc(100% - 18px)"></div>
      </div>
      <div class="chart">
        <div class="sub-head">
          <div class="title">个人偏科</div>
        </div>
        <div id="branch" style="width:100%;height:calc(100% - 18px)"></div>
      </div>
    </div>
  </div>
</template>
<script>
import "echarts/lib/chart/pie";
import "echarts/lib/chart/bar";
import "echarts/lib/chart/radar";
import "echarts/lib/chart/line";
import "echarts/lib/component/tooltip";
import "echarts/lib/component/legend";
import "echarts/lib/component/title";
let date = new Date();
let echarts = require("echarts/lib/echarts");
export default {
  data() {
    return {
      imgSrc:
        "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541394127913&di=0557d44eb41c645b1691a060250da601&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fwallpaper%2F1603%2F31%2Fc2%2F19819788_1459402903342_800x800.jpg",
      name: "周慧琳",
      gender: "女",
      native: "福建福州",
      nation: "汉",
      political: "共青团员",
      signInDate: "2018-09",
      rollNumber: "123456",
      birthday: "2000-01-28",
      IDCard: "350500200001285530",
      chooseTest: 0,
      calendarLoading: true,
      dataArr: [],
      tableData: [
        {
          name: "语文",
          times: 18,
          rate: "96%"
        },
        {
          name: "语文",
          times: 18,
          rate: "96%"
        },
        {
          name: "语文",
          times: 18,
          rate: "96%"
        },
        {
          name: "语文",
          times: 18,
          rate: "96%"
        },
        {
          name: "语文",
          times: 18,
          rate: "96%"
        },
        {
          name: "语文",
          times: 18,
          rate: "96%"
        },
        {
          name: "语文",
          times: 18,
          rate: "96%"
        }
      ],
      allItems: 20,
      scheduleList: [
        {
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541416797065&di=310efa638007d933394eb82c77968936&imgtype=0&src=http%3A%2F%2Fwww.examebook.com%2Fimages%2F20160127%2F1eacb5d44287236c.jpg",
          title: "语文——高考冲刺题型精讲",
          progress: 86
        },
        {
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541416797065&di=310efa638007d933394eb82c77968936&imgtype=0&src=http%3A%2F%2Fwww.examebook.com%2Fimages%2F20160127%2F1eacb5d44287236c.jpg",
          title: "语文——高考冲刺题型精讲",
          progress: 86
        },
        {
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541416797065&di=310efa638007d933394eb82c77968936&imgtype=0&src=http%3A%2F%2Fwww.examebook.com%2Fimages%2F20160127%2F1eacb5d44287236c.jpg",
          title: "语文——高考冲刺题型精讲",
          progress: 86
        }
      ],
      yearMonth: "2018-11",
      homeWorkChartColors: ["#FF7171", "#3FABFF"],
      homeWorkChartDatas: [
        {
          value: 21,
          name: "已完成"
        },
        {
          value: 54,
          name: "未完成"
        }
      ],
      spendDate: "2018年11日",
      totalSpendTime: 821,
      catalogChoose: 2
    };
  },
  mounted() {
    this.dateHandler(new Date());
    this.drawDoneChart();
    this.drawAttentionOnChart();
    this.drawBranchChart();
    this.drawSpendTimeChart();
  },
  methods: {
    back() {
      this.$router.back();
    },
    // 生成日历数组
    async dateHandler(param) {
      this.dataArr = [];
      // 根据传递的参数创建时间
      let now = new Date(param * 1);
      let monthStartDate = new Date(now.setDate(1));
      // 获取本月一号对应的星期
      let monthStartWeekDay = monthStartDate.getDay();
      // 获取日历中第一行的第一个时间
      let dyBefore = new Date(now.setDate(-monthStartWeekDay));
      // 构造日期数组
      let tempLineArr = [];
      for (let i = 0; i < 6; i++) {
        let tempItemArr = [];
        for (let j = 0; j < 7; j++) {
          dyBefore.setDate(dyBefore.getDate() + 1);
          let curMon = false;
          if (dyBefore.getMonth() == date.getMonth()) {
            curMon = true;
          }
          let checked = false;
          if (
            dyBefore.getMonth() == date.getMonth() &&
            date.getDate() === dyBefore.getDate()
          ) {
            checked = true;
          }
          tempItemArr.push({
            check: checked,
            isCurMon: curMon,
            day: dyBefore.getDate(),
            classNum: 0
          });
          this.tempClassNum = 0;
        }
        tempLineArr.push({
          children: tempItemArr
        });
      }
      this.calendarLoading = false;
      this.dataArr = tempLineArr;
    },
    chooseDay(key, index) {},
    pickYear() {},
    drawDoneChart() {
      let scale = 1;
      let rich = {
        num: {
          fontSize: 15 * scale,
          padding: [8, 4],
          align: "center"
        },
        total: {
          fontSize: 36 * scale,
          align: "center"
        },
        percent: {
          fontSize: 20 * scale,
          padding: [5, 0],
          align: "center"
        },
        hr: {
          borderColor: "#0b5263",
          width: "100%",
          borderWidth: 1,
          height: 0
        }
      };
      let option = {
        title: {
          text: "课后作业",
          left: "center",
          top: "52%",
          padding: [12, 0],
          textStyle: {
            fontSize: 15 * scale,
            align: "center"
          }
        },
        legend: {
          selectedMode: false,
          formatter: name => {
            let total = 0; //各科正确率总和
            //let averagePercent; //综合正确率
            this.homeWorkChartDatas.forEach(function(value, index, array) {
              total += value.value;
            });
            return `{total|${total}}`;
          },
          data: [this.homeWorkChartDatas[0].name],
          left: "center",
          top: "32%",
          icon: "none",
          align: "center",
          textStyle: {
            rich: rich
          }
        },
        series: [
          {
            type: "pie",
            radius: ["58%", "72%"],
            hoverAnimation: false,
            color: this.homeWorkChartColors,
            label: {
              normal: {
                formatter: (params, ticket, callback) => {
                  let total = 0;
                  let percent = 0;
                  this.homeWorkChartDatas.forEach(function(
                    value,
                    index,
                    array
                  ) {
                    total += value.value;
                  });
                  percent = ((params.value / total) * 100).toFixed(1);
                  return `{percent|${percent}%}\n{hr|}\n{num|${
                    params.value
                  }份}`;
                },
                rich: rich
              }
            },
            labelLine: {
              normal: {
                length: 15 * scale,
                length2: 0,
                lineStyle: {
                  width: 2,
                  color: "#0b5263"
                }
              }
            },
            data: this.homeWorkChartDatas
          }
        ]
      };
      let chart = echarts.init(document.getElementById("doneChart"));
      chart.setOption(option);
    },
    drawAttentionOnChart() {
      let option = {
        xAxis: {
          type: "category",
          data: ["语文", "生物", "英语", "数学", "物理"],
          axisLabel: {
            color: "#bbb"
          },
          axisLine: {
            lineStyle: {
              color: "#ccc"
            }
          },
          axisTick: {
            show: false
          }
        },
        yAxis: {
          type: "value",
          show: false
        },
        grid: {
          top: "12%",
          bottom: "10%",
          left: "1%",
          right: "1%"
        },
        series: [
          {
            type: "bar",
            barWidth: 28,
            label: {
              show: true,
              position: "top",
              color: "#bbb",
              fontSize: 12,
              formatter: "{c}%"
            },
            data: [
              {
                value: 40,
                itemStyle: {
                  color: "#3FABFF"
                }
              },
              {
                value: 32,
                itemStyle: {
                  color: "#19D2D4"
                }
              },
              {
                value: 16,
                itemStyle: {
                  color: "#63D4E2"
                }
              },
              {
                value: 12,
                itemStyle: {
                  color: "#62F4EF"
                }
              },
              {
                value: 6,
                itemStyle: {
                  color: "#62F4D0"
                }
              }
            ]
          }
        ]
      };
      let chart = echarts.init(document.getElementById("attention"));
      chart.setOption(option);
    },
    drawBranchChart() {
      let option = {
        backgroundColor: "#FFF",
        tooltip: {
          trigger: "item"
        },
        radar: {
          nameGap: 8,
          radius: "78%",
          name: {
            textStyle: {
              color: "#999999",
              fontSize: 14
            }
          },
          splitArea: {
            areaStyle: {
              color: ["#fff", "#fff", "#fff", "#fff"],
              shadowColor: "rgba(0, 100, 0, 0.3)"
            }
          },
          indicator: [
            {
              name: "语文",
              max: 150
            },
            {
              name: "数学",
              max: 150
            },
            {
              name: "英语",
              max: 150
            },
            {
              name: "地理",
              max: 100
            },
            {
              name: "历史",
              max: 100
            },
            {
              name: "生物",
              max: 100
            },
            {
              name: "化学",
              max: 100
            },
            {
              name: "政治",
              max: 100
            }
          ]
        },
        series: [
          {
            name: "偏科情况",
            type: "radar",
            data: [
              {
                value: [140, 120, 140, 42, 80, 96, 86, 69],
                itemStyle: {
                  normal: {
                    color: "transparent",
                    borderColor: "#3FABFF",
                    borderWidth: "4"
                  }
                },
                areaStyle: {
                  normal: {
                    color: "rgba(63, 171, 255, 0.66)"
                  }
                }
              }
            ]
          }
        ]
      };
      let chart = echarts.init(document.getElementById("branch"));
      chart.setOption(option);
    },
    drawSpendTimeChart() {
      let option = {
        xAxis: {
          type: "category",
          splitLine: {
            show: true
          },
          axisTick: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#ccc"
            }
          },
          data: ["8", "9", "10", "11", "12", "13", "14", "15"]
        },
        color: "#7DC6FF",
        yAxis: {
          type: "value",
          axisTick: {
            show: false
          },
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: "#ccc"
            }
          }
        },
        grid: {
          top: "8%",
          bottom: "15%",
          left: "8%",
          right: "8%"
        },
        series: [
          {
            symbolSize: 0,
            data: [4, 2, 3, 1, 5, 4, 6, 1],
            type: "line"
          }
        ]
      };
      let chart = echarts.init(document.getElementById("spendTime"));
      chart.setOption(option);
    }
  }
};
</script>
<style lang="less">
.teacher-class-student-detail {
  width: 100%;
  .sub-head {
    width: 100%;
    height: 25px;
    .btns {
      float: left;
      margin-left: 22%;
      span {
        padding: 2px 5px;
        border-radius: 5px;
        margin-left: 8px;
        font-size: 0.8rem;
        &:hover {
          cursor: pointer;
        }
      }
      .choose-test {
        background: #3fabff;
        color: #fff;
      }
    }
    .date {
      width: 80px;
      height: 100%;
      float: right;
      font-size: 0.9rem;
      position: relative;
      .date-cover {
        width: 100%;
        height: 22px;
        line-height: 22px;
        position: absolute;
        left: 0;
      }
      .date-picker {
        width: 100%;
        height: 100% !important;
        position: absolute;
        left: 0;
        opacity: 0;
        &:hover {
          cursor: pointer;
        }
        .el-input__inner {
          height: 100% !important;
          line-height: 22px !important;
        }
        .el-input__icon {
          line-height: 22px !important;
        }
      }
    }
  }
  .first-panel {
    width: 100%;
    margin-top: 22px;
    height: 250px;
    .base {
      width: 62%;
      height: 100%;
      float: left;
      background: #fff;
      padding: 12px 22px;
      .box {
        width: 100%;
        .img-box {
          width: 105px;
          height: 125px;
          float: left;
          margin-top: 18px;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .detail {
          width: 396px;
          margin-left: 28px;
          text-align: left;
          padding-top: 15px;
          float: left;
          .line {
            width: 100%;
            height: 22px;
            margin-top: 9px;
            font-size: 0.9rem;
            .half {
              float: left;
              width: 40%;
              .label {
                color: #aaa;
              }
            }
            .last {
              width: 60% !important;
            }
          }
        }
      }
    }
    .calendar {
      width: 36%;
      height: 100%;
      float: right;
      background: #fff;
      padding: 12px 22px;
      .calendar-box {
        width: calc(100% + 44px);
        margin-left: -22px;
        .flex-box {
          display: flex;
          justify-content: space-between;
          .flex-item {
            width: 14%;
            height: 100%;
            text-align: center;
          }
        }
        .week-box {
          width: 100%;
          font-size: 0.8rem;
          color: #aaa;
        }
        .day-box {
          width: 100%;
          .day-line {
            width: 100%;
            height: 28px;
            text-align: left;
            .color-gray {
              color: #ccc !important;
            }
            .flex-item {
              position: relative;
              &:hover {
                cursor: pointer;
              }
              .date-item {
                height: 100%;
                line-height: 28px;
                font-size: 0.9rem;
                .day {
                  padding: 2px 6px;
                  border-radius: 50%;
                }
                .cur-day {
                  color: #fff !important;
                  background: #4ea9ff;
                }
                .day-dot {
                  width: 6px;
                  height: 6px;
                  border-radius: 50%;
                  background: #ff7171;
                  position: absolute;
                }
                .blue-dot {
                  background: #4ea9ff;
                }
              }
            }
          }
        }
      }
    }
  }
  .second-panel {
    width: 100%;
    margin-top: 22px;
    height: 525px;
    .grade {
      width: 62%;
      height: 100%;
      float: left;
      background: #fff;
      padding: 12px 22px;
      .grade-table {
        width: 100%;
        margin-top: 12px;
        color: #aaa;
      }
    }
    .right {
      width: 36%;
      height: 100%;
      float: right;
      .schedule {
        width: 100%;
        height: 48%;
        background: #fff;
        padding: 12px 22px;
        .box {
          width: 100%;
          height: calc(100% - 25px);
          overflow-y: scroll;
          padding-top: 8px;
          .schedule-item {
            width: 100%;
            height: 66px;
            border: 1px solid #aaa;
            margin-bottom: 18px;
            border-radius: 5px;
            .img-box {
              width: 66px;
              height: 100%;
              float: left;
              img {
                width: 100%;
                height: 100%;
              }
            }
            .desc {
              padding: 8px 12px;
              text-align: left;
              float: left;
              .title {
                margin-bottom: 6px;
                font-size: 0.9rem;
              }
            }
          }
        }
      }
      .done {
        width: 100%;
        height: 48%;
        margin-top: 6%;
        background: #fff;
        padding: 12px 22px;
        .diy-legend {
          height: 18px;
          .legend-item {
            height: 18px;
            margin-left: 12px;
            font-size: 0.9rem;
            span {
              vertical-align: middle;
            }
            .icon {
              width: 18px;
              height: 18px;
              display: inline-block;
              border-radius: 5px;
              margin-right: 2px;
            }
          }
        }
      }
    }
  }
  .third-panel {
    width: 100%;
    height: 250px;
    margin-top: 22px;
    display: flex;
    justify-content: space-between;
    .chart {
      width: 296px;
      height: 100%;
      background: #fff;
      padding: 12px 22px;
      .catalog {
        width: 100%;
        height: 20px;
        margin-top: 6px;
        .date-btn {
          float: left;
          padding: 1px 3px;
          border-radius: 5px;
          margin-right: 8px;
          font-size: 0.8rem;
          &:hover {
            cursor: pointer;
          }
        }
        .choosed {
          color: #fff;
          background: #3fabff;
        }
        .date {
          float: right;
          font-size: 0.9rem;
        }
      }
      .total-time {
        font-size: 1.52rem;
        color: #4ed6ff;
        span {
          color: #4ea9ff;
          font-size: 0.9rem;
        }
      }
    }
  }
}
</style>



